<template>
  <div class="card">
    <div class="card-header">
      Exam08View
    </div>
    <div class="card-body">
      <div>kind: {{route.params.kind}} </div>
      <div>color: {{route.params.color}} </div>
      <hr />
      <div>kind: {{route.query.kind}} </div>
      <div>color: {{route.query.color}} </div>
    </div>
  </div>
</template>

<!-- 화면전환을 router가 하기 때문에 값의 전달도 router가 한다
Router -> 이동을 시켜주는 역할을 해주는 객체(우편 배달부) Route -> 이동하는 Url의 정보를 갖고 있는 객체(우편) 
전달하는 자체가 목적이라면 Router를 사용해야 하고, 전달 된 데이터가 무엇인지 알기 위해서는 Route를 이용해야 한다 -->

<script setup>
  import { useRoute } from "vue-router";
  import { toRaw } from "vue";
  
  const route = useRoute(); //route에서 fullPath, name, params 등등 확인할 수 있다 
  console.log(route);
  console.log("-------------------------------");
  console.log(toRaw(route));
  console.log("-------------------------------");
  console.log(JSON.parse(JSON.stringify(route)));
  console.log("-------------------------------");
  console.log("kind: " + route.params.kind);
  console.log("color: " + route.params.color);
  console.log("-------------------------------");
  console.log("kind: " + route.query.kind);
  console.log("color: " + route.query.color);
</script>

