<template>
  <div class="card">
    <div class="card-header">
      Exam07View
    </div>
    <div class="card-body">
      <div class="card">
        <div class="card-header">
          Path Variables(Parameter)를 이용해서 전달
        </div>
        <div class="card-body">
          <li><router-link to="/menu01/exam08view/freeboard/blue">/menu01/exam08view/freeboard/blue</router-link></li>
          <li><router-link :to="`/menu01/exam08view/${kind1}/${color1}`">/menu01/exam08view/{{kind1}}/{{color1}}</router-link></li> <!-- 표현식. template안에서 값을 넣고 싶을 때 {{ }} 중괄호를 2개를 넣어서 사용한다. 주로 사용할 땐 이 방법을 사용한다.-->
          <li><router-link :to="{path:`/menu01/exam08view/${kind2}/${color2}`}">/menu1/exam08view/{{kind2}}/{{color2}}</router-link></li>
          <li><router-link :to="{name:'menu01_exam08view', params: {kind:kind3, color:color3}}">/menu01/exam08view/{{kind3}}/{{color3}}</router-link></li> <!-- 아직 정의 하지 않은 것(라우트에 등록하지 않은 상태)에 대해서는 나오지 않는다. 그러므로 route에서 등록을 하면 값이 제대로 들어간다. -->
        
          <li>
            <button @click="goUrlWithParams" class="btn btn-info btn-sm mr-2">/menu01/exam08view/{{kind3}}/{{color3}}</button>
            <a href="#" @click="goUrlWithParams" class="btn btn-info btn-sm mr-2">/menu01/exam08view/{{kind3}}/{{color3}}</a> 
          </li>
        </div>
      </div>

      <div class="card">
        <div class="card-header">
        QueryString을 이용해서 전달
        </div>
        <div class="card-body">
          <li><router-link to="/menu01/exam08view?kind=freeboard&color=blue">/menu01/exam08view?kind=freeboard&color=blue</router-link></li>
          <li><router-link :to="`/menu01/exam08view/?kind=${kind1}&color=${color1}`">/menu01/exam08view/kind={{kind1}}&color={{color1}}</router-link></li> 
          <li><router-link :to="{path:'/menu01/exam08view', query: {kind:kind2, color:color2}}">/menu01/exam08view/kind={{kind2}}&color={{color2}}</router-link></li>
          <li><router-link :to="{name:'menu01_exam08view', query: {kind:kind3, color:color3}}">/menu01/exam08view/kind={{kind3}}&color={{color3}}</router-link></li> 
          <li>
            <button @click="goUrlWithQueryString" class="btn btn-info btn-sm mr-2">/menu01/exam08view/kind={{kind3}}&color={{color3}}</button>
            <a href="#" @click="goUrlWithQueryString" class="btn btn-info btn-sm mr-2">/menu01/exam08view/kind={{kind3}}&color={{color3}}</a> 
          </li>
        </div>
      </div>
    </div>
  </div>
</template>

<script setup>
  import { useRouter } from "vue-router";
  const router = useRouter();

  const kind1 = "notice";
  const color1 = "red";

  const kind2 = "qa";
  const color2 = "green";

  const kind3 = "album";
  const color3 = "yellow";

  function goUrlWithParams() {
    router.push(`/menu01/exam08view/${kind3}/${color3}`);
    //router.push({name:'menu01_exam08view', params: {kind:kind3, color:color3}}); //잘 쓰이지 않음
  }

  function goUrlWithQueryString() {
    router.push(`/menu01/exam08view/?kind=${kind3}&color=${color3}`); //push는 promise이다. 비동기로 처리 됨. //url에 노출되기 때문에 중요한 데이터를 넘길때는 사용해선 안된다.
  }

</script>

<style scoped></style>
